import React, { Component } from 'react'
import { Carousel } from 'react-bootstrap'
import { Swipeable } from 'react-touch'
import Breakjs from 'breakjs'
import './Hero.css'
import img1 from '../../assets/images/slider-1.jpg'
import img2 from '../../assets/images/slider-2.jpg'
import img3 from '../../assets/images/slider-3.jpg'

const layout = Breakjs({
  mobile: 0,
  desktop: 768,
});

class Hero extends Component {

  resizeThrottler() {
    let resizeTimeout;
    if (!resizeTimeout) {
      resizeTimeout = setTimeout(() => {
        resizeTimeout = null;
        this.updateDimensions();
      }, 33);
    }
  }

  updateDimensions() {
    let ol = document.getElementsByClassName('carousel-inner')[0].offsetLeft;
    let list = document.getElementsByClassName("carousel-caption");
    for (var i = 0; i < list.length; i++) {
        list[i].style.left = Math.abs(ol) + 30 + "px" 
    }
  }

  componentDidMount() {
    this.resizeThrottler();
    window.addEventListener("resize", this.resizeThrottler.bind(this), false);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resizeThrottler.bind(this));
  }

  slideLeft = () => {
    if (layout.current() === "mobile") {
      this.carouselInstance.handleNext()  
    }
  }

  slideRight = () => {
    if (layout.current() === "mobile") {
      this.carouselInstance.handlePrev()  
    }
    
  }

  render() {
    return (
      <div id="hero" className="hero">
        <Swipeable ref={(swiper) => this.swipeInstance = swiper } onSwipeLeft={this.slideLeft} onSwipeRight={this.slideRight}>
          <Carousel ref={(carousel) => this.carouselInstance = carousel } interval={5000}  controls={false} slide={true} pauseOnHover={false}>
            <Carousel.Item>
              <img alt="Hot Chicks Taking Pics" src={img3} />
              <Carousel.Caption>
                <h3>Screening:<br />DVF Secret Agent Part 2</h3>
                <h4>October 15th, 2017</h4>
                <p>Join us for a private screening of DVF Secret Agent Part 2<br />with our special guest, director Peter Lindberg.</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
              <img alt="Hot Chicks Shoes" src={img2} />
              <Carousel.Caption>
                <h3>Screening:<br />DVF Secret Agent Part 2</h3>
                <h4>October 15th, 2017</h4>
                <p>Join us for a private screening of DVF Secret Agent Part 2<br />with our special guest, director Peter Lindberg.</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
              <img alt="Hot Chicks" src={img1} />
              <Carousel.Caption>
                <h3>Screening:<br />DVF Secret Agent Part 2</h3>
                <h4>October 15th, 2017</h4>
                <p>Join us for a private screening of DVF Secret Agent Part 2<br />with our special guest, director Peter Lindberg.</p>
              </Carousel.Caption>
            </Carousel.Item>
          </Carousel>
        </Swipeable>
      </div>
    )
  }
};

export default Hero;


