import React, { Component } from 'react'
import { Grid, Row, Col, Breadcrumb, Glyphicon } from 'react-bootstrap'
import './Header.css'
import logo from '../../assets/images/logo.png'

class Header extends Component {

  render() {

    const topLinks = (
      <Breadcrumb>
        <Breadcrumb.Item href="tel:+01-555-555-5555" className="header-link" >
          555.555.5555
        </Breadcrumb.Item>
        <Breadcrumb.Item href="#" className="header-link">
          Login
        </Breadcrumb.Item>
        <a href="#" onClick={ this.toggleNavigation } className="nav-toggle pull-right">
          <Glyphicon glyph="menu-hamburger" />
        </a>
      </Breadcrumb>
    )

    return (
      <header id="header">
        <Grid fluid={true}>
          <Row>
            <Col xs={4} sm={6} className="logo-col">
              <img src={logo} className="brand-logo" role="presentation"  />
            </Col>
            <Col xs={8} sm={6} className="link-col">
              <div className="pull-right header-links">
                {topLinks}
              </div>
            </Col>
          </Row>
        </Grid>
      </header>
    );
  }
}

export default Header;

      


