import React, { Component } from 'react'
import { Grid, Row, Col  } from 'react-bootstrap'
import Breakjs from 'breakjs'
import './Articles.css'
import img1 from '../../assets/images/image-1.jpg'
import img2 from '../../assets/images/image-2.jpg'
import img3 from '../../assets/images/image-3.jpg'
import img4 from '../../assets/images/image-4.jpg'
import img5 from '../../assets/images/image-5.jpg'
import img6 from '../../assets/images/image-6.jpg'


const layout = Breakjs({
  mobile: 0,
  desktop: 768,
});

class Articles extends Component {

  componentDidMount() {
    layout.addChangeListener(this.onLayoutChange);
  }

  state = {
    layout: layout.current(),
    mod: layout.current() === "desktop" ? 3 : 2
  }

  static defaultProps = {

    articleData: [
          { 
            img: img1,
            title: "Bureaux exquisite delightful carefully curated soft power.",
            date: "February 10 2017" 
          },
          { 
            img: img2,
            title: "Sharp bureaux sleepy K-pop carefully curated.",
            date: "February 2 2017" 
          },
          { 
            img: img3,
            title: "St Moritz uniforms Beams.",
            date: "January 27 2017" 
          },
          { 
            img: img4,
            title: "Esse airport veniam ryokan soft power.",
            date: "January 21 2017" 
          },
          { 
            img: img5,
            title: "K-pop extraordinary.",
            date: "January 18 2017" 
          },
          { 
            img: img6,
            title: "Artisanal iconic cutting-edge business class.",
            date: "January 12 2017"
          }
        ]
  }

  onLayoutChange = (layout) => {
    this.setState({
      layout: layout,
      mod: layout === "desktop" ? 3 : 2
    });
  }

  render() {
    
    let cols = [[],[],[]];
    let mod = this.state.mod;

    this.props.articleData.map((article, i) => {

      let col = (
        <div key={i} className="article-col">
          <img src={article.img} alt={article.title} className="entry-image" />
          <p className="entry-date">{article.date}</p>
          <h3 className="entry-title">{article.title}</h3>
          <p className="entry-author">Presented by: <a href="#">Lorem Ipsum</a></p>
        </div>
      )
      cols[i % mod].push(col)
      return false
    })

    const colMap = cols.map((col, i) => {
      return (
        <Col xs={6} sm={4} key={i}>
          {col.map((c) => c )}
        </Col>
      )
    })

  
    return (
      <div className="articles">
        <Grid fluid={true}>
          <Row>
            {colMap}
          </Row>
        </Grid>
      </div>
    )
  }
}

export default Articles;


